<template>
  <div
    v-loading="!params.data.length"
    :element-loading-background="$STANDARD.loadingBgColor"
    :id="randomID"
    :style="{width:params.styleObj.width,height:params.styleObj.height}"
  ></div>
</template>

<script>
  import {mockData} from "@/utils/common";
  import "amcharts3/amcharts/pie";
  import "amcharts3/amcharts/themes/light";

  export default {
    name: "PieChart3D",
    props: {
      animateFlag: Boolean,
      defaultHigh: Number,
      params: {
        type: Object,
        default() {
          return {
            id: "amcharts",
            data: [],
            styleObj: {
              width: "100%",
              height: "100%"
            },
            angle: 45
          };
        }
      }
    },
    data() {
      return {
        mychart: null,
        chartData: []
      };
    },
    mounted() {
      this.initChart(false);
    },

    methods: {
      initChart(flag) {
        // let _colors = ["#70A33B", "#307A8D", "#CA9440", "#C24E29", "#A56066", "#BF1B28", "#007941", "#CBB824", "#AD10AD", "#C67B21", "#FC6", "#6FF", "#93C", "#039", "#105EA0"];
        let _colors =
          this.params.color === undefined
            ? this.$STANDARD.pieMoreColors
            : this.params.color;

        this.mychart = new AmCharts.AmPieChart();

        let newData = JSON.parse(JSON.stringify(this.params.data));
        this.mychart.dataProvider = mockData(newData);
        if (this.mychart.dataProvider.length === 0) return;

        this.mychart.numberFormatter = {
          precision: 1,
          decimalSeparator: ".",
          thousandsSeparator: ""
        };
        this.mychart.percentPrecision = 0;
        this.mychart.pullOutOnlyOne = true;//单击扇形区域的变化（true其他弹出扇形收回，false不收回）
        this.mychart.pullOutRadius = 0;//点击扇形区域后的拉出半径，0为不拉出
        this.mychart.depth3D = this.params.depth3D ? this.params.depth3D : 10;
        this.mychart.angle = this.params.angle ? this.params.angle : 40;
        this.mychart.radius = this.params.radius ? this.params.radius : "35%";
        this.mychart.innerRadius = 0;//内部半径
        this.mychart.labelRadius = this.params.labelRadius ? this.params.labelRadius : 35;//标签线长度
        if (flag) {
          this.mychart.sequencedAnimation = true;
          this.mychart.startEffect = "easeOutSine";
          this.mychart.startDuration = 0.5;
        } else {
          this.mychart.sequencedAnimation = false;
          this.mychart.startDuration = 0;
        }

        //扇形区域展示的title 和 value
        this.mychart.titleField = "name";
        this.mychart.valueField = "value";
        // this.mychart.animationPlayed = false;
        //扇形颜色
        this.mychart.colors = _colors;
        //标签颜色
        this.mychart.color = "#fff";
        //标签字体大小
        this.mychart.fontSize = this.params.fontSize ? this.params.fontSize : 16;
        // this.mychart.marginTop = 50;

        let total = 0;
        for (let [index, item] of this.params.data.entries()) {
          total += item.value;
        }
        // 弹框
        this.mychart.balloonFunction =  (param) =>{
          if(parseInt(total)===0){
            return `${param.title}: 0% (0)`;
          }else{
            return `${param.title}: ${param.percents.toFixed(2)}% (${param.value.toFixed(0)})`;
          }
        };
        //label标签
        this.mychart.labelFunction = param => {
          if(parseInt(total)===0){
            return `${param.title} \n 0%`;
          }else{
            if (this.params.isBr) {
              if (this.params.labelPer) {
                return `${param.title} \n ${param.percents.toFixed(2)}%`
              } else {
                return `${param.title} \n ${param.value}`
              }
            } else {
              return `${param.title}`
            }
          }
        };
        //标签线条透明度
        this.mychart.labelTickAlpha = 0.7;
        //标签线条颜色
        this.mychart.labelTickColor = "#fff";

        this.mychart.precision = 0;

        this.mychart.write(this.randomID);

        if (this.defaultHigh !== undefined) {
          this.mychart.clickSlice(this.defaultHigh);
        }

        this.mychart.addListener("clickSlice", params => {
          params.allData = {
            data: params.chart.dataProvider,
            params: {name: params.dataItem.title}
          };
          this.$emit("canvasClick", params.allData);
        });
      }
    },
    computed: {
      randomID() {
        return Math.random().toString(16).slice(2, 10);
      },
      styleType() {
        return this.$store.state.styleType;
      }
    },
    watch: {
      animateFlag: {
        handler(newVal, oldVal) {
          this.initChart(true);
          this.mychart.clickSlice(1);
        }
      },
      'params.data': {
        handler(newVal, oldVal) {
          this.initChart(true);
        },
        deep: true
      }
    }
  };
</script>

