// 3D饼图当数据全为0时，饼图不显示，此方法填充模拟数据
const MOCKVALUE = 0.00001;
function mockData(data) {
  let total = 0;
  for (let [index, item] of data.entries()) {
    total += item.value;
  }
  if(total===0){
    for (let [index, item] of data.entries()) {
      item.value = MOCKVALUE;
    }
  }
  return data
}
export { mockData }
