            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#fa71cd", "#c471f5"]]
            },
            map: {
              value: "数量"
            }
          }
        },
        //8.信访量走势
        xflzsLineParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          colors: [
            {
              line: "#e11770",
              start: "rgba(225, 23, 112, .5)",
              end: "rgba(225, 23, 112, .5)"
            },
            {
              line: "#ffb435",
              start: "rgba(255, 180, 53, .5)",
              end: "rgba(255, 180, 53, .5)"
            },
            {
              line: "#1a82f7",
              start: "rgba(26, 130, 247, .5)",
              end: "rgba(26, 130, 247, .5)"
            },
            {
              line: "#a1d871",
              start: "rgba(161, 216, 113, .5)",
              end: "rgba(161, 216, 113, .5)"
            },
            {
              line: "#ff825b",
              start: "rgba(255, 130, 91, .5)",
              end: "rgba(255, 130, 91, .5)"
            }
          ],
          option: {
            splitNumber: 4,
            axisLabelColor: "#fff",
            yaxisLabelColor: "#82d8ff",
            legendfontSize: 18,
            legendShow:false,
            splitLineShow: true,
            legenditemWidth: 22,
            legenditemHeight: 4,
            axisLabelShow: true,
            axisTickShow: false,
            axisCategoryName: "",
            axisYName: "",
            axisLabelSize: 14,
            scales: true,
            axisLabelFontSize: 16,
            yaxisLabelFontSize: 14,
            Tofixed: false,
            smooth: true,
            areaShow: 0,
            smoothMonotone: "none",
            dataZoomshow: false,
            left: "5%",
            right: "10%",
            top: "15%",
            bottom: "10%",
            legendTop: "5%",
            legendRight: "10%",
          },
          map: {
            value: "数量"
          },
          xName: "name"
        },
        // 9.集体访分布
        jtffbBarParams: {
          data: [],
          canvasStyle: {
            width: "100%",
            height: "100%"
          },
          options: {
            isVertical:true,
            xName: "name", //x轴字段
            axisValueShow: false,
            axis1Name: "人次",
            axisLabelSize: 14, // label字号
            left: "8%",
            right: "0%",
            top: "5%",
            bottom: "0%",
            barLabelOptions: "top",
            barWidth: "50%",
            barBorderRadius: 5,
            splitLineShow: false,
            barLabelShow: false, //柱状图数量值显示
            axisLabelColor: "rgba(0, 176, 255, .8)",
            itemLabelColor: "rgba(0, 176, 255, .8)",
            colors: {
              bar: [["#7061ff", "#5cd9ff"]]
            },
            map: {
              value: "数量"
            }
          }
        },
      }
    },
    mounted() {
      for (let key in this.mapJson) {
        if (this.mapJson[key] === this.cookieCity) {
          this.curCity = key
        }
      }
      this.mapParams.options.JSON = this.cookieCity;
      this.getMapAPI(this.date);
    },
    methods: {
      // 4.全省信访量-地图
      getMapAPI(date) {
        // 处理接口参数
        // 省级
        if (this.curCity === '广东省') {
          this.shej_02 = this.curCity;
          this.shij_02 = 'invalid';
          this.xj_02 = 'invalid';
          this.nameP = 'shij_02';
        } else {
          // 市级
          if (this.shijCity) {
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = 'invalid';
            this.nameP = 'xj_02';
          } else {
            // 县区级
            this.shej_02 = '广东省';
            this.shij_02 = this.shijCity;
            this.xj_02 = this.curCity;
            this.nameP = 'invalid';
          }
        }
        get_http("xfywtlMap", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.mapParams.options.map = res.data.map;
            this.mapParams.data = res.data.data;
            this.mapFlag = true;
            this.getAPI(date)
          })
          .catch(err => {
            console.log(err);
          });
      },
      getAPI(date) {
        // 1.信访目的分布
        get_http("xfywtlXfmdPie", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.xfmdPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 2.信访形式占比
        get_http("xfywtlXfxsPie", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.xfxsPieParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 3.热点问题分布
        get_http("xfywtlRdwtCloud", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.rdwtCloudParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 5.全省信访量-件次总数（地图左上角）
        get_http("xfywtlJctotal", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.jctotal = res.data.data[0].value;
          })
          .catch(err => {
            console.log(err);
          });
        // 6.全省信访量-各类件次（地图左上角）
        get_http("xfywtlJcType", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.jcType = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 7.区域信访量排行
        get_http("xfywtlXflphBar", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.xflphBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 8信访量走势
        get_http("xfywtlXflzsLine", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.xflzsLineParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
        // 9.集体访分布
        get_http("xfywtlJtffbBar", {
          shej_02: this.shej_02,
          shij_02: this.shij_02,
          xj_02: this.xj_02,
          name: this.nameP,
          day: `[${date.startDate},${date.endDate}]`
        })
          .then(res => {
            this.jtffbBarParams.data = res.data.data;
          })
          .catch(err => {
            console.log(err);
          });
      },
      // 点击地图下钻
      handleMap(name) {
        this.curCity = name;
        for (let key in this.mapJson) {
          if (this.curCity === key) {
            this.mapFlag = false;
            this.mapParams.options.JSON = this.mapJson[name];
          }
        }
      },
      // 地图返回全省
      mapBack() {
        this.mapFlag = false;
        this.curCity = "广东省";
        this.mapParams.options.JSON = "440000";
      },
    },
    computed: {
      cookieCity(){
        return Cookies.get('xzqhdm')
      },
      date() {
        return this.$store.state.date;
      }
    },
    watch: {
      curCity: function (newVal, oldVal) {
        if (newVal === '广东省') {
          this.getMapAPI(this.date);
        } else {
          let c = '';
          for (let key in this.mapJson) {
            if (newVal === key) {
              this.shijCity = newVal;
              c = newVal;
              this.getMapAPI(this.date);
            }
          }
          if (!c) {
            this.xj_02 = newVal;
            this.getAPI(this.date)
          }
        }
      },
      date(val) {
        this.getMapAPI(val);
      }
    }
  }
</script>

